@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{color:inherit}a:hover{color:inherit;text-decoration:none;background-color:rgba(255,255,255,0.7)}.App{background:url(/tourabu-expedition-calculator/static/media/j-lee-LYBSBjGo-5s-unsplash.95fe5256.jpg) no-repeat right center;background-attachment:fixed;background-size:cover;font-family:'Open Sans', sans-serif;min-height:100vh}@media (max-width: 768px){.App{background-position:right -200px center}}.App-wrapper{max-width:520px;padding:15px;width:100%}.App-header{border-bottom:1px solid rgba(255,255,255,0.7);padding-bottom:5px;margin-bottom:10px}.App-title{font-size:2rem;margin-bottom:30px;margin-left:5px;text-shadow:1px 1px 0px #fff;letter-spacing:0.1em}.App-footer a{display:inline-flex;font-size:10px;align-items:center;color:inherit;padding:2px}.App-footer a img{margin-right:5px}.App-content{min-height:calc(100vh - 200px)}@media (max-width: 768px){.App-content{min-height:calc(100vh - 280px)}}.material-selector{margin-bottom:5px}.material-selector label{position:relative;margin:0 5px;cursor:pointer}.material-selector label>span{display:flex;align-items:center;font-size:14px;padding:1px 5px}.material-selector label>span img{width:30px;height:auto;margin-right:5px;flex-grow:0;flex-shrink:0}.material-selector label>span span{text-transform:capitalize}.material-selector label input{visibility:hidden;position:absolute}.material-selector label input:checked+span{box-shadow:0 0 0 2px rgba(255,255,255,0.8);border-radius:2px;background:rgba(255,255,255,0.3)}.custom-control{margin-left:10px}.custom-control label{font-size:14px;line-height:26px}.showResults>.result{display:flex;margin:5px 5px 10px;font-size:12px;border:1px solid #fff;padding:5px;align-items:center}.showResults>.result h2{flex:0 0 50px;font-size:1.5em;border-right:1px solid #fff;text-align:center;display:block;padding:10px 0;margin-right:15px}.showResults>.result h2 small{font-size:12px;display:block;margin-top:2px}.showResults>.result>.result-content{display:flex;flex-basis:100%}.showResults>.result>.result-content>div{flex:1 1}.showResults>.result>.result-content ul{margin:0;padding:0;list-style:none;display:flex;flex-wrap:wrap;flex-basis:100%}.showResults>.result>.result-content ul>li{flex-basis:50%}.showResults>.result>.result-content .requires{font-size:80%;position:relative;padding:1px 0 1px 9px;flex:0 0 30%}.showResults>.result>.result-content .requires:before{content:'* ';display:block;position:absolute;top:2px;left:0;font-size:14px;line-height:1em}.showResults>.result>.result-content .icon-plus{display:block;margin-top:3px;font-size:80%}.showResults>.result>.result-content .icon-plus:before{content:'+ '}.startText{font-size:14px;padding:15px 0}

